.sunset {
    position: relative;
    min-height: 600px;
    background: url('../img/bg/what_we_do_footer_bg.jpg')  center (center/cover) no-repeat;
    .container {
        .sunset__descr {
            position: absolute;
            width: 298px;
            margin-top: 280px;
            margin-left: 1080px;
            text-align: left;
            color: aliceblue;
        }
        .sunset-head {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            /* identical to box height, or 143% */
            letter-spacing: 2px;
            text-transform: uppercase;
        
        }
        .sunset-descr {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            /* or 143% */
            color: #C0C0C0;
        }
       
    }
   
    .photo_info {
        width: 100%;
        display: flex;
        justify-content: flex-end; 
        font-size: 12px;
        padding-left: 1040px;
        transform: translateY(550px);
        padding-right: 60px;
    }
    .text__time {
        position: relative;
        @include before_photo-time;
        @include text_color;
    }
    .text__camera {
        position: relative;
        padding-left: 30px;
        @include before_photo-camera;
        @include text_color;
        font-size: 12px;
    }
    .text__iso {
        position: relative;
        padding-left: 30px;
        @include before_photo-iso;
        @include text_color;
        font-size: 12px;
    }
    .text__map {
        position: relative;
        padding-left: 30px;
        @include before_photo-map;
        @include text_color;
        font-size: 12px;
    }
}