.work {
    background: #000000;
    padding: 242px 242px 136px 136px;
   
    &__header {
        font-weight: 400;
        font-size: 40px;
        line-height: 42px;
        /* identical to box height, or 105% */
        text-align: center;
        @include text_color
    }
    &__subheader {
        font-weight: 400;
        font-size: 23px;
        line-height: 30px;
        /* identical to box height, or 130% */
        text-align: center;
        color: #C0C0C0;
    }
    &__container {
        margin-top: 40px;
        display: grid;
        grid-template-columns: 577px 600px;
        gap: 16px;
        padding-left: 150px;
        
    }
    .wrapper__work-items {
        position: relative;
    }
    button {
        position: absolute;
        height: 42px;
        left: 0%;
        right: 72.29%;
        top: 544px;
        left: 16px;
        @include before_work_btn;
        a {
            text-decoration: none;
            color: black;
        }
    }
    .work__item-text {
        position: absolute;
        bottom: 8px;
        left: 16px;
        color: aliceblue;
        bottom: 90px;
        width: 500px;
       
    }
    .work__item-texthead {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        /* or 143% */
        letter-spacing: 2px;
        text-transform: uppercase;
        text-align: start;
    }
    .work__item-textsub {
       
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        /* or 143% */
        color: #C0C0C0;
    }
}